// @flow

import * as React from 'react';

type Props = {||};

function Menu(props: Props): React.Node {
  return (
    <nav id="menu">
      <h2>Menu</h2>
      <ul>
        <li>
          <a href="/">Home</a>
        </li>
      </ul>
    </nav>
  );
}

export default Menu;



